import React, { Component } from 'react';
import {isMobile} from 'react-device-detect';
import './Intro.css';

class Intro extends Component {

    render() {
        function scrollToExperience(e) {
            var experience = document.getElementById("experience");
            experience.scrollIntoView({ block: 'start',  behavior: 'smooth' });
        }
        function scrollToSkills(e) {
            var experience = document.getElementById("skills");
            experience.scrollIntoView({ block: 'start',  behavior: 'smooth' });
        }
        function openLinkedIn(){
            window.open('https://in.linkedin.com/in/harish-annamalai', '_blank');
        }

        function openCommunication(){

            if(isMobile){
                window.location.href = "tel:+919986786846";
            }else{
                window.location.href = "mailto:cerium11@gmail.com";
            }            
            
        }

        return (
            <div id="intro" className="page-div">
                <div className=" background-image mask">
                    <div className="flex-layout">
                    <div className="left-intro">
                    </div>
                    <div className="right-intro">
                        <div className="cover-text">
                            <div id="name-box">
                                <h1 className="text-center">Harish Annamalai</h1>
                                <h2 className="text-center">Fullstack Java Developer</h2>
                            </div>
                            <div id="contact-box">
                                <h3 className="text-center">+91 9986 786 846</h3>
                                <h3 className="text-center">cerium11@gmail.com</h3>
                            </div>

                            <div className="icons">
                                <i className="fa fa-briefcase" onClick={scrollToExperience} ></i>
                                <i className="fab fa-black-tie" onClick={scrollToSkills} ></i>
                                <i className="fab fa-linkedin" onClick={openLinkedIn} ></i>
                                <i className="fab fa-whatsapp-square" onClick={openCommunication}></i>
                            </div>
                        </div>
                    </div>
                    </div>
                    
                </div>
            </div>
        );
    }

}

export default Intro;
